@import "../../global.scss";


.portfolio {
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  .left{
    flex: 0.5;
    background-color: white;
    overflow: hidden;

    .wrapper{
      width: 90%;
      height: 100%;
      flex-direction: column;
      margin-left: 20px;
      justify-content: center;

      @include mobile{
        margin-top: -80px;
      }

      h1{
        margin-left: 20px;
        margin-top: 110px;
        font-size: 30px;

        @include mobile{
          overflow: hidden;
          margin-top: 90px;
        }

        span{
          font-size: 50px;
          color: #008080;
        }
      }
      p{
        font-size: 17px;
        margin-top: 50px;
        font-weight: 300;
        display: flex;
        line-height: 1.7;
        letter-spacing: 1px;
        text-indent: 20px;
        margin-left: 20px;
        justify-content: center;

        @include mobile{
          align-items: center;
          margin-right: 20px;
          margin-top: -2px;
          overflow: hidden;
          font-size: 12px;
          justify-content: center;
        }
      }
    }
  }

  .right{
    flex: 0.5;
    background-color: white;
    overflow: hidden;


    .imgContainer{
      width: 550px;
      height: 550px;

      @include mobile{
        align-items: center;
        margin-top: -30px;
        width: 300px;
        height: 300px;

      }

      img{
        width: 100%;
        height: 80%;
      }
    }
  }

}
